@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans&display=swap);
code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

/* Global styles */
:root {
  --main-color: #eaeaea;
  --secondary-color: #fff;

  --border-color: #c1c1c1;

  --main-text: black;
  --secondary-text: #4b5156;

  --theme-dot-border: #24292e;

  --preview-background-color: rgba(251, 249, 243, 0.8);
  --preview-shadow-color: #f0ead6;

  --button-color: black;

  --a-color: #17a2b8;
}

html,
body {
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
strong {
  color: var(--main-text);
  font-family: "Russo One", sans-serif;
  font-weight: 500;
}

p,
li,
span,
label,
input,
textarea {
  color: var(--secondary-text);
  font-family: "Roboto Mono", monospace;
}

a {
  text-decoration: none;
  color: var(--a-color);
  font-family: "Roboto Mono", monospace;
}

ul {
  list-style: none;
}

h1 {
  font-size: 56px;
}

h2 {
  font-size: 36px;
}
h3 {
  font-size: 28px;
}

h4 {
  font-size: 24px;
}

h5 {
  font-size: 20px;
}

h6 {
  font-size: 16px;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.s1 {
	background-color: var(--main-color);
	border-bottom: 1px solid var(--border-color);
	overflow: auto;
}

.s2 {
	background-color: var(--secondary-color);
	border-bottom: 1px solid var(--border-color);
	overflow: auto;
}

.main-container {
	width: 1200px;
	margin: 0 auto;
}

@media screen and (max-width: 1200px) {
	.main-container {
		width: 95%;
	}
}

.intro-wrapper {
  background-color: var(--secondary-color);
  margin-top: 50px;

  border: 1px solid var(--border-color);
  border-radius: 5px 5px 0 0;
  box-shadow: -1px 1px 3px -1px rgba(0, 0, 0, 0.75);

  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "nav-wrapper nav-wrapper" "left-column right-column";
}

.nav-wrapper {
  grid-area: nav-wrapper;

  background-color: var(--main-color);
  border-bottom: 1px solid var(--border-color);
  border-radius: 5px 5px 0 0;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-wrapper-left__dots-wrapper {
  padding: 10px;
  display: flex;
}

.browser-dot {
  height: 15px;
  width: 15px;
  margin: 5px;
  background-color: black;

  border-radius: 50%;
  box-shadow: -1px 1px 3px -1px rgba(0, 0, 0, 0.75);
}

.dot-1 {
  background-color: #fc6058;
}

.dot-2 {
  background-color: #fec02f;
}

.dot-3 {
  background-color: #2aca3e;
}

.nav-wrapper-right__navigation-list {
  margin: 0;
  padding: 10px;
}

.nav-wrapper-right__navigation-list li {
  display: inline-block;
  margin-right: 5px;
  margin-left: 5px;
}

.left-column {
  grid-area: left-column;

  padding-top: 50px;
  padding-bottom: 50px;
}

.left-column h5,
.left-column p {
  text-align: center;
  line-height: 0;
}

.avatar {
  height: 200px;
  width: 200px;
  object-fit: cover;
  /* border: 2px solid var(--border-color); */

  display: block;
  margin: 0 auto;
}

.right-column {
  grid-area: right-column;

  display: grid;
  align-content: center;

  padding-top: 50px;
  padding-bottom: 50px;
}

.right-column__preview-shadow {
  background-color: var(--preview-shadow-color);
  width: 300px;
  height: 180px;
  padding-left: 30px;
  padding-top: 30px;
}

.right-column__preview {
  width: 300px;
  border: 1.5px solid #17a2b8;
  background-color: var(--preview-background-color);
  padding: 15px;

  position: relative;
}

.corner {
  width: 7px;
  height: 7px;
  background-color: #fff;

  border: 1.5px solid #17a2b8;
  border-radius: 50%;

  position: absolute;
}

.corner-tl {
  top: -5px;
  left: -5px;
}

.corner-tr {
  top: -5px;
  right: -5px;
}

.corner-bl {
  bottom: -5px;
  left: -5px;
}
.corner-br {
  bottom: -5px;
  right: -5px;
}

@media screen and (max-width: 800px) {
  .intro-wrapper {
    grid-template-columns: 1fr;
    grid-template-areas: "nav-wrapper" "left-column" "right-column";
  }
  .right-column {
    justify-content: center;
    padding-top: 0;
  }
}

@media screen and (max-width: 500px) {
  .right-column {
    padding-top: 0;
  }

  .right-column__preview {
    width: 240px;
    height: 120px;
  }

  .right-column__preview h3 {
    margin: 0 0 10px 0;
  }
  .right-column__preview p {
    margin-top: 0;
    font-size: 14px;
  }
}

.about-wrapper {
	padding: 10px 0 50px 0;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
	grid-gap: 100px;
	gap: 100px;
}

.about-left-bot__socials-icons {
	display: flex;
	/* justify-content: center; */
}

.about-left-bot__socials-icons img {
	padding: 10px;
}

.about-left-bot__socials-icons img:first-child {
	padding-left: 0px;
}

.about-right__skills {
	display: flex;
	flex-direction: column;
	background-color: var(--preview-shadow-color);
	padding-bottom: 11px;
}

.about-right__skills-languages p,
.about-right__skills-tools p {
	padding-left: 10px;
}

.about-right__skills-languages img,
.about-right__skills-tools img {
	padding: 5px;
}

@media screen and (max-width: 500px) {
	.about-wrapper {
		grid-gap: 50px;
		gap: 50px;
	}

	.about-right h4 {
		margin-top: 0;
	}
}

.posts-wrapper {
	padding-bottom: 50px;
}

.post {
	margin: 0 10px 0 10px;
	height: 305px;
	background-color: #fff;
	overflow: hidden;

	border: 1px solid var(--border-color);
	border-radius: 5px 5px 0 0;
	box-shadow: -1px 1px 3px -1px rgba(0, 0, 0, 0.75);
}

.post__thumbnail {
	display: block;
	width: 100%;
	height: 180px;
	object-fit: contain;
	background-color: rgba(0, 0, 0, 0.2);
}

.post__preview {
	padding: 10px;
}

.post__title {
	color: black;
	margin: 0;
	overflow: hidden;
	display: block;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 1;
}

.post__description {
	color: #4b5156;
	font-size: 14px;
	overflow: hidden;
	display: block;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 2;
}

.greeting-wrapper {
  display: grid;
  text-align: center;
  align-content: center;
}

.greeting-wrapper p {
  font-size: 15px;
}

.blocks {
	height: calc(100vh - 80px);
	width: 100vw;
	overflow: hidden;
	--primary: #fff;
	min-height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	background: rgba(246, 194, 73, 0.8);
}

/**
  * infinity-path
  *
  * @author jh3y
*/
.infinity-path {
	--size: 120;
	--speed: 0.65;
	height: calc(var(--size) * 1px);
	position: relative;
	width: calc(var(--size) * 1px);
}
.infinity-path > div {
	height: calc(var(--size) * 1px);
	width: calc(var(--size) * 1px);
	border-radius: 100%;
	border: calc(var(--size) / 4 * 1px) solid rgba(0, 0, 0, 0.05);
	position: absolute;
	top: 0;
	left: 0;
	animation-duration: calc(var(--speed) * 1s);
	animation-timing-function: linear;
	animation-iteration-count: infinite;
	animation-name: infinity-spin;
	transform: translate(calc(var(--translate) * 1%), 0)
		translate(calc(var(--translate-2) * 1px), 0);
}
.infinity-path > div:before {
	content: "";
	height: calc(var(--size) / 4 * 1px);
	width: calc(var(--size) / 4 * 1px);
	border-radius: 100%;
	background: var(--primary);
	position: absolute;
	top: 50%;
	animation: infinity-vanish calc(var(--speed) * 2s) infinite reverse steps(1);
	transform: translate(
		calc(var(--translate-2) * 2px),
		calc(var(--translate) * 1%)
	);
}
.infinity-path > div:nth-of-type(1) {
	--translate: -50;
	--translate-2: calc(var(--size) / 8);
}
.infinity-path > div:nth-of-type(1):before {
	right: 0;
}
.infinity-path > div:nth-of-type(2) {
	--translate: 50;
	--translate-2: calc(var(--size) / 8 * -1);
	animation-delay: calc(var(--speed) * 1s);
	animation-direction: reverse;
}
.infinity-path > div:nth-of-type(2):before {
	left: 0;
	transform: translate(calc(var(--size) / 4 * -1px), -50%);
	animation-direction: normal;
}

@keyframes infinity-vanish {
	0% {
		opacity: 0;
	}
	50% {
		opacity: 1;
	}
}
@keyframes infinity-spin {
	from {
		transform: translate(calc(var(--translate) * 1%), 0)
			translate(calc(var(--translate-2) * 1px), 0) rotate(0deg);
	}
	to {
		transform: translate(calc(var(--translate) * 1%), 0)
			translate(calc(var(--translate-2) * 1px), 0) rotate(360deg);
	}
}

.body {
	background-color: #95c2de;
	height: calc(100vh - 80px);
	display: flex;
	justify-content: center;
	align-items: center;
}

.mainbox {
	background-color: #95c2de;
	margin: auto;
	height: 600px;
	width: 600px;
	position: relative;
}

.err {
	color: #ffffff;
	font-family: "Nunito Sans", sans-serif;
	font-size: 11rem;
	position: absolute;
	left: 20%;
	top: 8%;
}

.far {
	position: absolute;
	font-size: 8.5rem;
	left: 42%;
	top: 15%;
	color: #ffffff;
}

.err2 {
	color: #ffffff;
	font-family: "Nunito Sans", sans-serif;
	font-size: 11rem;
	position: absolute;
	left: 68%;
	top: 8%;
}

.msg {
	text-align: center;
	font-family: "consolas", sans-serif;
	font-size: 2rem;
	position: absolute;
	left: 16%;
	top: 45%;
	width: 75%;
}

.a {
	text-decoration: none;
	color: white;
}

.a:hover {
	text-decoration: underline;
}

.app {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  max-width: 400px;
  min-height: 100vh;
  margin: 30px auto;
  padding: 0 50px;
  overflow: hidden;

  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
}

.scrollToTop {
  position: fixed;
  width: 100%;
  left: 30px;
  bottom: 50px;
  height: 20px;
  font-size: 40px;
  z-index: 999;
  cursor: pointer;
  color: rgb(26, 140, 215);
}

.app img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
}

.information h2 {
  font-size: 30px;
  margin-bottom: 10px;
  padding-bottom: 0;
  font-weight: bold;
  font-family: "Source Sans Pro", sans-serif;
}

.information h4 {
  font-size: 20px;
}

.social {
  margin: 10px 0;
  width: 80%;
  padding: 5px;
  border: 1px solid var(--border-color);
  border-top-right-radius: 40px;
  -moz-border-radius-topright: 40px;
  -webkit-border-top-right-radius: 40px;
  border-bottom-left-radius: 40px;
  -moz-border-radius-bottomleft: 40px;
  -webkit-border-bottom-left-radius: 40px;
  border-top-left-radius: 10px;
  border-bottom-right-radius: 10px;
  box-shadow: -1px 3px 3px -1px rgba(0, 0, 0, 0.25);
  transition: all 0.2s ease-in;
}

.social:hover {
  transform: scale(1.05);
}

.social a {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
  color: black;
  padding: 0 10px;
}

.social a .icon img {
  width: 50px;
  height: 50px;
}

.social a p {
  font-size: 20px;
}

.intro-wrapper1 {
  background-color: var(--secondary-color);
  width: 100%;

  border: 1px solid var(--border-color);
  border-radius: 5px 5px 0 0;
  box-shadow: -1px 1px 3px -1px rgba(0, 0, 0, 0.75);
}

.information {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}

@media screen and (max-width: 350px) {
  .social a p {
    font-size: 14px;
  }
}

@media screen and (max-width: 600px) {
  .social a p {
    font-size: 16px;
  }
  .information h2 {
    font-size: 24px;
  }
}

